import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
// import store from '@/store.js';

Vue.use(Router);

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
    ]
});


export default router;